import React from 'react';
import HeaderMenu from './HeaderMenu';
import HeaderTop from './HeaderTop';

class Header extends React.Component {
    constructor(props) {
        super(props)
    }

    render() {
        return (
            <div className='header'>
                <HeaderTop />
                <HeaderMenu />
            </div>
        )
    }
}

export default Header;


