import React from 'react';

class HeaderMenu extends React.Component {
    constructor(props) {
        super(props)
    }

    render() {
        return (
            <div className='header-menu'>
                <div className='header-menu-btns'>
                    <ul>
                        <li><a href="#">Биржа</a></li>
                        <li><a href="#">Категории</a></li>
                        <li><a href="#">Логисты</a></li>
                        <li><a href="#">Помощь</a></li>
                        <li><a href="#">Выставки</a></li>
                        <li><a href="#">Аудиторские компании</a></li>
                    </ul>
                </div>
                <div className='header-menu-right'>
                    <div className='header-menu-currency'>
                        <button>
                            <img src='https://sun9-65.userapi.com/NO7bMIzsqtkjWdyfZ6VKqYGlo_lEi3SuytXSWw/ruGfyI85Ti4.jpg' alt='currency'/>
                            <p>USD</p>
                        </button>
                    </div>
                    <div className='header-menu-language'>
                        <button>
                            <img src='https://sun3-12.userapi.com/EGzdeRNmAvCs6_BM0yyk3LQYSYjlOaI3r96bEw/eMuTnVnUqLg.jpg' alt='language'/>
                            <p>RUS</p>
                        </button>
                    </div>
                </div>
            </div>
        )
    }
}

export default HeaderMenu;


