import React from 'react';

class SignIn extends React.Component {
    constructor(props) {
        super(props)
    }

    render() {
        return (
            <div className='signin-btn'>
                <a href='signin' onClick={this.props.onclick}>
                    <img src='https://sun9-3.userapi.com/etKtLTQ9OuJodps6GqIA4VFzwe-qFiJWWtod0Q/5YAOfKceW6w.jpg' alt='signin_icon'/>
                    Войти
                </a>
            </div>
        )
    }
}

export default SignIn;


