import React from 'react';
import Finder from './Finder';
import SignInButton from './SignInButton';

class HeaderTop extends React.Component {
    constructor(props) {
        super(props)
    }

    render() {
        return (
            <div className='header-top'>
                <Finder />
                <SignInButton />
            </div>
        )
    }
}

export default HeaderTop;


